body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body, #root {
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.test {
  text-align: center;
  position: relative;
  width: 100%;
  height: 100%;
}

.test__content {
  padding: 200px;
}

.test__content--no-pointer {
  pointer-events: none;
}

.test__overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 90%;
  height: 90%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: pointer;
  background: -webkit-radial-gradient(closest-side, rgba(255, 255, 200,1) 0%, rgba(0,0,0,0) 100%);
  background: radial-gradient(closest-side, rgba(255, 255, 200,1) 0%, rgba(0,0,0,0) 100%);
  -webkit-transition: opacity 300ms cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: opacity 300ms cubic-bezier(0.215, 0.61, 0.355, 1);
  opacity: 0.2;
}

.test__overlay:hover {
  opacity: 0.5;
}

.test__overlay--hidden {
  opacity: 0;
  pointer-events: none;
}
.app {
  width: 100%;
  height: 100%;
}

.app__content {
  width: 100%;
  height: 100%;
}

.app__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  pointer-events: none;
}

.app__overlay button {
  pointer-events: auto;
}

